

.filter-conteiner .filter-top h2 {
    font-size: 22px;
    font-family: "Poppins", sans-serif;
}

.filter-conteiner .filters {
    margin-top: 24px;
    box-shadow: 0px 2px 6px #E5E5E5;
    border-radius: 10px;
    color: #424242;
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    background-color: #fff;
}
.filter-conteiner .filter-list{
    padding-top: 8px;
    padding-bottom: 8px;
    position: relative;
}
.filter-conteiner .filter-list .filter-item{
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 4px;
    padding-bottom: 4px;
    width: 100%;
    display: flex;
    position: relative;
    box-sizing: border-box;
    text-align: left;
    align-items: center;
    justify-content: flex-start;
    text-decoration: none;
}
.filter-conteiner .filter-list .filter-item input[type=checkbox] {
    background: #fff;
    appearance: none;
    -webkit-appearance: none;
    border: 1px solid #d3d4dd;
    cursor: pointer;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 22px;
    width: 22px;
    outline: none;
}

.filter-conteiner .filter-list .filter-item input[type=checkbox]::after {
    content: "\f00c";
    font-family: "Font Awesome 5 Free";
    font-size: 18px;
    color: white;
    font-weight: 900;
    display: none;
}

.filter-conteiner .filter-list .filter-item input[type=checkbox]:checked {
    background-color: #ed174a;
}

.filter-conteiner .filter-list .filter-item input[type=checkbox]:checked::after {
    display: block;
}
.auto-completed{
    width: 100%;
}
.auto-completed .input-base{
    width: 400px;
    border-radius: 10px;
    background-color: #fff;
    color: #424242;
    cursor: text;
    display: inline-flex;
    position: relative;
    font-size: 1rem;
    box-sizing: border-box;
    align-items: center;
    font-family: Noto Sans Armenian, sans-serif, Arial, Open Sans, "Segoe UI", "Segoe UI Emoji", "Segoe UI Symbol", "Helvetica Neue", "Apple Color Emoji";
    font-weight: 400;
    line-height: 1.1876em;
}
.search-icons{
    height: auto;
    display: flex;
    max-height: 2em;
    align-items: center;
    white-space: nowrap;
    margin-left: 8px;
}
.search-icons svg{
    width: 24px;
    height: 24px;
    color: #7d7c7c !important;
}
.auto-completed .search{
    flex-grow: 1;
    text-overflow: ellipsis;
    padding: 10px;
    font-size: 15px;
    letter-spacing: 1px;
    font: inherit;
    color: currentColor;
    width: 100%;
    border: 0;
    height: 1.1876em;
    margin: 0;
    display: block;
    background: none;
    box-sizing: content-box;
    min-width: 0;
}
.product-item{
    width: 30%;
    margin: 5px;
    overflow: hidden;
    box-shadow: 0px 2px 6px #E5E5E5;
    background-color: #fff;
}
.product-content{
    padding: 16px;
    padding-bottom: 16px;
}
.add-to-cart{
    background-color: #ed174a;
    color: #fff;
    font-family: "poppins", sans-serif;
    padding: 6px;
    border-radius: 3px;
}
.filter-btns button {
    background: transparent linear-gradient(180deg, #c5022e 0%, #ea1f4d 100%) 0% 0% no-repeat;
    padding: 10px 18px;
    font-weight: bold;
    line-height: 22px;
    text-transform: inherit;
    color: #fafafa;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-family: Noto Sans Armenian, sans-serif, Arial, Open Sans, "Segoe UI", "Segoe UI Emoji", "Segoe UI Symbol", "Helvetica Neue", "Apple Color Emoji";
    font-size: 0.875rem;
    border-radius: 8px;
}
.product-show-more button{
    width: 100%;
    padding: 12px;
    background-color: #ed174a;
    text-align: center;
    color: #fff;
    font-size: 17px;
    font-family: "poppins", sans-serif;
}
