.header .image {
    max-width: 200px;
}

.header {
    transition: all 0.15s cubic-bezier(0.28, 0.12, 0.22, 1);
}

.header.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    background-color: white;
    box-shadow: 0 0 10px 5px #eee;
}

.header .wrapper nav ul .menu-item a {
    position: relative;
    font-size: 17px;
    font-weight: 600;
    line-height: 1;
    letter-spacing: 0;
    height: 2.5rem;
    font-family: "Dosis", sans-serif;
    padding-left: 1.125rem;
    padding-right: 1.125rem;
    border-radius: 2.5rem;
    transition: all 0.15s cubic-bezier(0.28, 0.12, 0.22, 1);
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.header .wrapper nav ul .menu-item a.active {
    color: #2bbef9 !important;
    background-color: #f0faff;
}

.cart-count {
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    width: 18px;
    height: 20px;
    text-align: center;
    font-size: 14px;
    border-radius: 50%;
    z-index: 1;
    background-color: #ed174a;
}

.open-nav {
    display: none;
}

.open-nav i {
    font-size: 25px;

}