@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: '#f9f9fb'
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  width: 100%;
  cursor: pointer;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

button {
  outline: none;
  cursor: pointer;
}

i {
  cursor: pointer;
}

input {
  outline: none;
}

header.active .conteiner {
  margin-top: 150px;
}

.empty-text {
  font-family: "poppins", sans-serif;
  font-size: 22px;
  color: #000;
  font-weight: 600;
}

.title {
  font-size: 23px;
  font-weight: normal;
  letter-spacing: 0.1px;
  font-family: "poppins", sans-serif;
}

.add-to-cart:active {
  transform: scale(0.9);
}

.add-to-cart.exist {
  background: white;
  color: red;
  border: 1px solid #ed174a;
}

.add-to-cart.exist:active {
  transform: scale(1);
}

.text-modal {
  position: fixed;
  top: -100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 250px;
  padding: 15px;
  border-radius: 5px;
  color: #fff;
  font-family: "poppins", sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  transition: top 100ms ease-in-out 200ms;
  background-color: black;
}

.text-modal.show {
  top: 20%;
}

.filter-checkbox {
  background: #fff;
  appearance: none;
  -webkit-appearance: none;
  border: 1px solid #d3d4dd;
  cursor: pointer;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 22px;
  width: 22px;
  outline: none;
}


.filter-checkbox.active {
  background-color: #ed174a;
}

.filter-checkbox.active i {
  display: block;
}

.filter-checkbox i {
  color: #fff !important;
  display: none;
}

.filter-item {
  cursor: pointer;
}

.multy-item {
  max-width: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 11px;
  padding: 7px;
  width: 100%;
  background-color: #c5023098;
  margin: 0 5px;
  color: #fff;
}

.multy-item span {
  font-size: 22px;
  cursor: pointer;
}

@media screen and (max-width: 1505px) {
  .modal-img-wrapper .image img {
    width: 100%;
  }
}

@media screen and (max-width: 1124px) {
  .modal-img-wrapper .image img {
    width: 100%;
  }

  .timer-content {
    width: 100% !important;
  }

  .filter-conteiner {
    width: 20% !important;
  }

  .uniform-content {
    display: flex !important;
    justify-content: center !important;
    flex-direction: column !important;
  }

  .uniform-shoping-conteiner {
    display: grid !important;
    grid-template-columns: repeat(3, 1fr);
  }

  .restoran-product {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .restoran-product .product-item {
    width: 100% !important;
    height: 100% !important;
  }

  .cart {
    display: flex !important;
    justify-content: center !important;
    flex-direction: column !important;
    width: 100% !important;
  }

  .cart .cart-content {
    width: 100% !important;
  }

  .cart .cart-total {
    width: 60%;
    margin: 25px auto !important;
  }
}

@media screen and (max-width: 1107px) {
  body {
    overflow-x: hidden;
  }

  .aside-filter {
    width: 25% !important;
  }

  .uniform-shoping-conteiner {
    width: 100% !important;
  }

  .uniform-shoping-conteiner .product-uniform-items {
    width: 100% !important;
  }
}

@media screen and (max-width: 735px) {
  .restoran-food {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
  }

  .filter-conteiner {
    width: 100% !important;
    margin-bottom: 10px !important;
  }

  .auto-completed {
    width: 100% !important;
  }

  .restoran-product {
    width: 100% !important;
    padding-top: 0 !important;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .restoran-product .product-item {
    width: 100% !important;
  }

  .uniform-content .content.active h1 {
    font-size: 40px !important;
  }

  .uniform-content .picture .image {
    width: 400px !important;
  }

  .uniform-product {
    display: flex !important;
    align-items: center !important;
    flex-direction: column !important;
  }

  .aside-filter {
    width: 100% !important;
  }

  .uniform-shoping-conteiner {
    display: grid !important;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 15px;
  }

  .uniform-shoping-conteiner .product-uniform-items {
    width: 100% !important;
  }

  .cart .cart-total {
    width: 100%;
  }

  .modal-content {
    width: 100% !important;
  }

  .modal-img-path {
    width: 100% !important;
  }

  .modal-product {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    text-align: center !important;
    justify-content: center !important;
  }
}

@media screen and (max-width: 551px) {
  .open-nav {
    display: block !important;
  }

  .header .navigation {
    position: fixed;
    top: 12%;
    right: -100%;
    z-index: 12;
    box-shadow: 0px 2px 6px #E5E5E5;
    background-color: #fff;
    padding: 12px;
    transition: right 0.3s ease-in-out;
  }

  .header .navigation.active {
    right: 12px !important;
  }

  .header .navigation ul {
    display: flex !important;
    text-align: center !important;
    flex-direction: column !important;
  }

  .header .navigation ul li {
    padding: 12px;
  }

  .slider-conteiner {
    max-height: 50vw !important;
  }

  .product-timer {
    flex-direction: column !important;
  }

  .restoran-product {
    width: 100% !important;
    padding-top: 0 !important;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .restoran-product .product-item {
    width: 100% !important;
  }

  .carusel-conteiner {
    width: 100% !important;
    max-width: 100% !important;
  }

  .carusel-conteiner .carusel-wrapper {
    width: 700% !important;
    padding: 8px !important;
  }

  .conteiner {
    padding: 8px !important;
  }

  .uniform-shoping-conteiner {
    display: grid !important;
    grid-template-columns: repeat(1, 1fr);
    padding-top: 15px;
  }

  .uniform-shoping-conteiner .product-uniform-items {
    width: 100% !important;
  }
}