.slider-conteiner {
    width: 100%;
    max-height: 30vw;
    overflow: hidden;
    position: relative;
    border-radius: 7px;
}

.owl-carusel {
    display: flex;
    width: 300%;
}

.owl-carusel .image {
    width: 300%;
}

.owl-carusel .image img {
    object-fit: cover;
}

.controls {
    position: absolute;
    z-index: 2;
    bottom: 8%;
    left: 6%;
}

.controls .dott-item {
    width: 0.5rem;
    height: 0.5rem;
    background-color: #89898a;
    margin: 0 5px;
    cursor: pointer;
    border-radius: 50%;
    transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
}

.controls .dott-item.active {
    background-color: #202435;
}

.timer-content {
    position: relative;
    border: 1px solid #ed174a;
    border-radius: 7px;
    padding: 1.875rem;
    width: 50%;
}

.product-timer {
    display: flex;
    -webkit-box-orient: horizontal;
    align-items: center;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
}

.product-wrapper {
    margin-right: 1.25rem;
    background: transparent;
}

.discount {
    position: absolute;
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    font-family: "Dosis", sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
    width: 4.125rem;
    height: 4.125rem;
    top: 0;
    left: 0;
    color: #fff;
    border-radius: 50%;
    background-color: #ed174a;
    z-index: 3;
}

.timer-price {
    display: block;
    font-size: 1.125rem;
    color: #ed174a;
    font-weight: 600;
}

.timer-name {
    font-size: 1.125rem;
    font-weight: 500;
    font-family: "Inter", sans-serif;

}
.progress{
    background-color: #ed174a;
}
.product-days-hours > div{
    margin: 0 5px;
    background-color: #edeef5;
    color: #3e445a;
    padding: 8px 12px;
    font-size: 17px;
    border-radius: 5px;
}