.cart-item .picture .image {
    width: 100px;
    height: 100px;
}

.cart-item .picture .image img {
    height: 100%;
    object-fit: cover;
}

.cart-content {
    width: 50%;
}

.cart-table {
    color: #212529 !important;
    width: 100%;
    background-color: transparent;
}

.cart-table tr {
    width: 100%;
}

.cart-table thead tr th {
    color: #71778e;
    border-bottom: 1px solid #e4e5ee;
    font-size: 17px;
    font-weight: 600;
    border-top: 0;
    padding: .75rem;
    vertical-align: bottom;
}

.cart-table tbody {
    position: relative;
}

.cart-table tbody tr {
    position: relative;
}

.cart-table tbody td:first-child {
    padding-left: 0;
}

.cart-table tbody td {
    border: none;
    border-top: 1px solid #dee2e6;
    font-size: .875rem;
    vertical-align: middle;
}

.cart-table tbody .cart-price {
    color: #71778e;
    font-weight: 500;
    font-size: 17px;
}

.cart-table tbody .quantity-items {
    webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row;
    flex-flow: row;
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.cart-table tbody .quantity-items .quantity-btns {
    width: 1.900rem;
    height: 1.900rem;
    background-color: #edeef5;
    border-radius: 50%;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    display: inline-flex;
    -webkit-box-align: center;
    font-size: 14px;
    margin: 0 8px;
    cursor: pointer;
} 
.name span{
    display: block;
    font-size: 17px;
    margin-bottom: 3px;
}
.descrement.disabled {
    opacity: 0.4;
}
.actions svg{
    color: #ed174a !important;
    cursor: pointer !important;
}
.actions span{
    display: inline-block;
}
.actions span:nth-child(2){
    margin-left: 8px;
    cursor: pointer;
}
.cart-futter button{
    padding: 12px 30px;
    color: #fff;
    font-family: "poppins", sans-serif;
    background-color: #ed174a;
    border-radius: 3px;
}
.cart-futter button:active{
    transform: scale(0.9);
}
.cart-total{
    width: 20%;
    height: 160px;
    padding: 16px 20px;
    box-shadow: 30px 50px 30px #dededc;
}
.length, .total-price{
    font-size: 16px;
    color: #71778e;
}
.final-total{
    margin-top: 5px;
    font-size: 25px;
    color: black;
    font-family: "poppins", sans-serif;
    font-weight: 600;
}
.total-btns{
    width: 100%;
}