.content.active {
    filter: blur(33px);
    opacity: 0;
    transform: translateY(80px);
    animation: fadeIn 0.8s ease-in-out forwards;
}

@keyframes fadeIn {
    to {
        filter: blur(0);
        opacity: 1;
        transform: translateY(0);
    }
}

.content h1 {
    font-size: 56px;
    line-height: 1.2em;
    width: 350px;
    font-weight: 600;
    color: rgba(0, 0, 0);
}

.content p {
    font-size: 20px;
    line-height: 1.5em;
}

.carusel-conteiner {
    max-width: 1250px;
    width: 100%;
    height: auto;
    position: relative;
    overflow: hidden;
}

.arrow {
    font-size: 30px !important;
    color: red;
}

.owl-dott {
    width: 12px;
    height: 12px;
    cursor: pointer;
    margin: 0 4px;
    border-radius: 50%;
    transition: all 0.3s ease-in-out;
    background-color: #d6d6d6;
}

.owl-dott.active {
    background-color: #ed174a;
}

.carusel-conteiner .carusel-wrapper {
    display: flex;
    width: 495%;
}

.carusel-conteiner .carusel-wrapper .image {
    width: 100%;
}

.owl-carusel-item {
    padding: 12px 20px;
    width: 100%;
}