.modal-conteiner {
    position: fixed;
    top: 0;
    left: 0;
    background: #0b0b0b8e;
    opacity: 0.8;
    z-index: 1042;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease-in-out;
    opacity: 0;
    visibility: hidden;
}

.modal-conteiner.show {
    opacity: 1;
    visibility: visible;
}

.modal-conteiner .modal-content {
    position: relative;
    background: #FFF;
    width: auto;
    width: 80%;
    border-radius: 7px;
    transform: translateY(-50%);
    font-family: "poppins", sans-serif;
    opacity: 0;
    transition: all 0.4s ease-in-out;
    visibility: hidden;
}

.modal-conteiner.show .modal-content {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
}


.modal-close {
    text-align: right;
    margin-right: 5px;
    font-size: 36px;
    color: #0b0b0b;
    cursor: pointer;
}

.modal-close:hover {
    opacity: 0.7;
}

.modal-item {
    padding: 2.5rem;
}

.modal-item .modal-item-header {
    border-bottom: 1px solid #e4e5ee;
}

.modal-item .modal-item-header h1 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: .9375rem;
}

.name-brand {
    margin-right: 4px;
    color: #9b9bb4;
    padding: 0;
    font-size: .75rem;
    font-weight: 400;
}

.brand-text {
    margin: 0;
    font-weight: 500;
    color: #3e445a;
    font-size: .75rem;
    letter-spacing: 0;
    line-height: 24px;
    font-family: "Inter", sans-serif;
}

.img-item {
    width: 102px;
    height: 102px;
    border: 1px solid #e4e5ee;
    margin: 0 6px;
    padding: 7px;
    border-radius: 6px;
    transition: all 0.3s ease-in-out;
}

.img-item.active {
    border: 1px solid #ed174a;
}

.img-item img {
    height: 100%;
}

.modal-img-wrapper {
    overflow: hidden;
    width: 100%;
    height: 480px;
}
.show-more-img .image{
    width: 50px;
    height: 50px;
}
.modal-img-wrapper .image {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: transform 0.5s ease-in-out;
}

.modal-img-wrapper .image img {
    width: 80%;
    height: 100%;
    border-radius: 4px;
    object-fit: cover;
}

.price {
    font-weight: 500;
    font-size: 20px;
}

.price-total {
    color: #d51243;
}

.price-del {
    color: #c2c2d3;
    font-size: 22px;
}

.text p {
    font-weight: 400;
    letter-spacing: 0;
    line-height: 24px;
    font-family: "Inter", sans-serif;
    font-size: 2em;
    color: #3e445a;
}

.btn button {
    padding: 12px 40px;
}
.modal-quantity{
    display: inline-flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.modal-quantity .quantity-btns{
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 2.75rem;
    height: 2.75rem;
    background-color:#edeef5;
    transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
    border-radius: 50%;
    font-size: 23px;
    cursor: pointer;
}
.modal-quantity .quantity-number{
    width: 2.625rem;
    height: 2.75rem;
    border: 0;
    padding: 0;
    margin: 0;
    background-color: transparent;
    text-align: center;
    pointer-events: none;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    color: #202435;
    font-family: "Inter", sans-serif;
}
.modal-quantity .quantity-btns:hover{
    background-color: #ffcd00;
    color: #202435;
}
.modal-quantity .minus.disabled{
    opacity: 0.5;
}
.modal-quantity .minus.disabled:hover{
    background-color:#edeef5;
}
.modal-txt{
    font-size: 17px;
    color: #00b853;
}
.all-description .tx{
    margin-bottom: 5px;
}
.all-description .tx .names{
    color: #c2c2d3;
}
.all-description .tx span:nth-child(2) {
    margin-left: 5px;
}