.product-uniform-items {
    width: 300px;
    padding: 8px;
    margin: 0 4px;
    font-family: "poppins", sans-serif;
    border-radius: 8px;
    background-color: #fff;
}

.product-uniform-items .image img {
    border-radius: 8px;
}

.product-uniform-items .product-price .original-price {
    color: #a73afd;
    font-size: 18px;
    font-weight: 700;
}

.del-price {
    font-size: 16px;
    color: #868695;
}

.product-uniform-description span:nth-child(1) {
    color: #242424;
}

.product-uniform-description span:nth-child(2),
span:nth-child(3) {
    color: #9d9da5;
    font-size: 14px;
    font-weight: 400;
}

.product-star svg {
    color: orange !important;
}

.product-uniform-items .add-to-cart {
    border-radius: 10px;
}

.aside-filter {
    padding: 15px;
    background-color: #fff;
    font-family: "poppins", sans-serif;
    border-top-right-radius: 8px;
    height: 100vw !important;
    transition: width 0.3s ease-in-out;
    border-bottom-right-radius: 8px;
    overflow: auto;
}

.aside-filter::-webkit-scrollbar {
    width: 10px;
    border-radius: 8px;
}

.aside-filter::-webkit-scrollbar-thumb {
    background-color: #e6e6e6;
    border-radius: 8px;
}

.product-title {
    font-size: 20px;
    font-weight: 500;
}

.aside-filter span i {
    color: #868695;
    transition: transform 0.3s ease-in-out;
}

.aside-filter .filter-item {
    display: flex;
    width: 100%;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
    justify-content: space-between;
    padding: 5px;
}

.aside-filter .filter-item:hover {
    background-color: #ebebed;
}

.aside-filter .filter-list li:hover {
    background-color: #ebebed;
}

.aside-filter .filter-list li {
    padding: 5px;
    cursor: pointer;
}

.aside-filter .filter-list {
    transition: max-height 0.3s ease;
}

.aside-filter .filter-item i {
    color: #9d9da5;
    font-size: 15px;
    transition: transform 0.3s ease;
}

.aside-filter input {
    margin-right: 6px;
}

.filter-text {
    color: #000;
    font-size: 17px;
    font-weight: 400;
}


.circle {
    width: 22px;
    height: 22px;
    border-radius: 50%;
}

.colors i {
    transform: scale(0);
    color: green;
}

.colors.check i {
    transform: scale(1);
}

.gender {
    max-height: 0;
    overflow: hidden;
}

.category {
    max-height: 0;
    overflow: hidden;
}

.brand {
    max-height: 0;
    overflow: hidden;
}

.color {
    max-height: 0;
    overflow: hidden;
}

.countrys {
    max-height: 0;
    overflow: hidden;
}

.size {
    max-height: 0;
    overflow: hidden;
}